.messageArea{border-left:5px solid grey;width:30%;height:100%;position:relative;display:inline-block;font-size:14px;vertical-align:top}.messageArea ul{height:calc(100vh - 30px);overflow-y:auto}.messageArea ul li{border:1px solid red;height:30px}.messageArea .inputArea{position:absolute;bottom:0;left:0;right:0;border:1px solid blue;height:30px}.messageArea .inputArea form{height:100%;width:100%;padding-top:3px;padding-bottom:3px}.messageArea .inputArea input{height:100%;width:calc(100% - 100px);margin-left:10px}.messageArea .inputArea button{height:100%;width:60px;margin-left:10px;margin-right:10px}


